import { Base, Type, markType } from "igniteui-core/type";
import { DashboardTile } from "./DashboardTile";
import { ComponentRenderer } from "igniteui-core/ComponentRenderer";
import { TypeDescriptionContext } from "igniteui-core/TypeDescriptionContext";
import { DashboardTileCommonFeature } from "./DashboardTileCommonFeature";
import { GeographicMapDescriptionModule } from "igniteui-core/GeographicMapDescriptionModule";

/**
 * @hidden 
 */
export class GeographicMapDashboardTileFeature extends Base {
	static $t: Type = markType(GeographicMapDashboardTileFeature, 'GeographicMapDashboardTileFeature');
	static register(): void {
		GeographicMapDashboardTileFeature.registerCore(DashboardTile.bs.context);
	}
	static registerCore(a: TypeDescriptionContext): void {
		DashboardTileCommonFeature.a(a);
		GeographicMapDescriptionModule.register(a);
	}
}


