		AxisDescriptionMetadata.d(a);
		NumericAxisBaseDescriptionMetadata.d(a);
		AnchoredCategorySeriesDescriptionMetadata.d(a);
		HorizontalAnchoredCategorySeriesDescriptionMetadata.d(a);
