import { IgcDataChartExtendedAxesModule } from 'igniteui-charts/igc-data-chart-extended-axes-module';
import { IgcDataChartRadialModule } from 'igniteui-charts/igc-data-chart-radial-module';
import { IgcDataChartPolarModule } from 'igniteui-charts/igc-data-chart-polar-module';
